// css reset
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}
html {
    font-size: 12px;
}
body {
    font-size: 1rem;
    min-width: 100%;
}

.game-wrapper {
    height: 100vh;
    width: 100vw;
    @include breakpoint (tablet) {
        width: 80%;
        min-width: 320px;
        height: 80vh;
        max-width: 1236px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin: 2vh auto;
    }
}
.title {
    display: none;
    @include breakpoint (tablet) {
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        align-items: center;
        font-size: 3vmax;
        font-weight: 300;
        color: $title-color;
        text-transform: uppercase;
        letter-spacing: 14px;
        margin-bottom: 20px;
        span {
            color: $regularText-color;
            font-size: 0.6em;
            padding-top: 10px;
            text-align: center;
            letter-spacing: 3px;
        } 
    }
}

%card { 
    width: 20vh;
    height: 20vh;
    min-width: 80px;
    min-height: 80px;
    border: 3px solid white;
    @include breakpoint (tablet) {
        width: 15vw;
        height: 15vw;
        max-width: 198px;
        max-height: 198px;
    } 
}
.size {
    @extend %card;
}
.game-container {
    height: 100vh;
    max-width: 40vh;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-self: center;
    align-items: flex-start;
    @include breakpoint (tablet) {
        height: auto;
        max-width: 1175px;
    }  
    .blue {
        background: url("../images/blue_monster.jpg") center center/cover no-repeat; 
    }
    .green {
        @extend %card;
        background: url("../images/green_monster.jpg") center center/cover no-repeat;
    }
    .pink {
        @extend %card;
        background: url("../images/pink_monster.jpg") center center/cover no-repeat;
    }
    .purple {
        @extend %card;
        background: url("../images/purple_monster.jpg") center center/cover no-repeat;
    }
    .purplelight {
        @extend %card;
        background: url("../images/purplelight_monster.jpg") center center/cover no-repeat;
    }
    .covered {
        @extend %card;
        background: url("../images/cards_bg.jpg") center center/cover no-repeat;
        border-radius: 9px;
    }
}
.flipping-animation {
	-webkit-animation: flip-in-hor-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: flip-in-hor-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
.hidden {
    visibility: hidden;
}
.restart-button {
    position: absolute;
    display: flex;
    justify-content: center;
    border-radius: 10px;
    background-color: $restartButton-color;
    width: 150px;
    height: 50px;
    font-size: 1rem;
    line-height: 50px;
    text-transform: uppercase;
    color: #fff;
    align-self: center;
    @include breakpoint (tablet) {
        align-self: flex-end;
        // margin-left: 45%;
    }
}

/* Generated by Animista
w: http://animista.net, t: @cssanimista */

@-webkit-keyframes flip-in-hor-bottom {
    0% {
      -webkit-transform: rotateX(80deg);
              transform: rotateX(80deg);
      opacity: 0;
    }
    100% {
      -webkit-transform: rotateX(0);
              transform: rotateX(0);
      opacity: 1;
    }
  }
  @keyframes flip-in-hor-bottom {
    0% {
      -webkit-transform: rotateX(80deg);
              transform: rotateX(80deg);
      opacity: 0;
    }
    100% {
      -webkit-transform: rotateX(0);
              transform: rotateX(0);
      opacity: 1;
    }
  }
